import {View} from "./Libs/View";
import mitt from 'mitt'
import React from 'react';
import ReactDOM from 'react-dom';
import logo from '/logo.svg';
import '/main.css';


class Button extends React.Component {

  render() {
    const { 
      variant,
      content,
      ...others
    } = this.props;
    
    return (
      <button className={variant} {...others}>
        {content}
      </button>
    )
  }
}

class TopButtons extends React.Component {
	constructor(props) {
		super(props);
		const emitter = mitt();
		
		this.state ={
			test: new View(emitter),
			bollocks : 'bollocks'
		};
    }
	
	render() {
		return (
			   <div>
				<Button content="Sample Button" variant="green" />
				<Button content="Matt is Awesome!" />
				<Button content="Sample Button" variant="red" />
			  </div>);
	}
}


class DiagramContainer extends React.Component {
	constructor(props) {
		super(props);
		const emitter = mitt();
		
		this.state ={
			test: new View(emitter),
			bollocks : 'bollocks'
		};
    }
	
	render() {
		return (
			  <div className="App">
				< TopButtons />
			 
			  </div>);
	}
}

ReactDOM.render(<DiagramContainer />, document.getElementById('root'));

// Hot Module Replacement
if (module.hot) {
  module.hot.accept();
}